<template>
  <div>
    <table class="table table-bordered">
      <tr>
        <th>Total Deposits (BGN):</th>
        <td class="text-right">{{ data.totalDepositsBGN }} BGN</td>
      </tr>
      <tr>
        <th>Total Withdrawals (BGN):</th>
        <td class="text-right">{{ data.totalWithdrawalsBGN }} BGN</td>
      </tr>
      <tr>
        <th>Total Commissions (BGN):</th>
        <td class="text-right">{{ data.totalCommissionsBGN }} BGN</td>
      </tr>
      <tr>
        <th>Gross Profit (BGN):</th>
        <td class="text-right">{{ data.grossProfitBGN }} BGN</td>
      </tr>
      <tr>
        <th>Net Profit (BGN):</th>
        <td class="text-right">{{ data.netProfitBGN }} BGN</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  props: ['data'],
}
</script>



