<template>
  <div>
    <table class="table table-bordered">
      <tr v-if="roi.last30days">
        <th>Last 30 days:</th>
        <!-- <td class="text-right">{{ roi.last30days.percentage }}%</td> -->
        <td class="text-right">{{ roi.last30days.amountBGN }} BGN</td>
      </tr>
      <tr v-if="roi.fromYearStart">
        <th>From the begining of the year:</th>
        <!-- <td class="text-right">{{ roi.fromYearStart.percentage }}%</td> -->
        <td class="text-right">{{ roi.fromYearStart.amountBGN }} BGN</td>
      </tr>
      <tr v-if="roi.fromPortfolioStart">
        <th>From the begining of the contract:</th>
        <!-- <td class="text-right">{{ roi.fromPortfolioStart.percentage }}%</td> -->
        <td class="text-right">{{ roi.fromPortfolioStart.amountBGN }} BGN</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  props: {
    roi: {},
  },
}
</script>



